import React, { useContext } from "react"
import { FaLinkedin, FaGithub, FaDev } from "react-icons/fa"
import { MdEmail } from "react-icons/md"
import "react-tippy/dist/tippy.css"
import { Tooltip } from "react-tippy"
import Section from "../../common/Section"
import { Heading, Tagline, CnTower, LinkContainer, Link } from "./styles"
import ThemeContext from "../../theme/Context/ThemeContext"

const Intro = () => {
  const {
    theme: { darkMode },
  } = useContext(ThemeContext)

  return (
    <Section.Container id="home">
      <Heading>Hi, I'm Sbastien!</Heading>
      <Tagline darkMode={darkMode}>
        Full Stack Developer from
        <Tooltip
          title="Toronto"
          position="bottom"
          trigger="mouseenter"
          arrow={false}
        >
          <CnTower
            src="data:image/svg+xml;utf8;base64,"
            alt=""
            darkMode={darkMode}
          />
        </Tooltip>
      </Tagline>

      <LinkContainer darkMode={darkMode}>
        <Link
          target="_blank"
          href="https://www.linkedin.com/in/sbeaury"
          rel="noreferrer"
        >
          <FaLinkedin />
        </Link>
        <Link
          target="_blank"
          href="https://github.com/sbeaury"
          rel="noreferrer"
        >
          <FaGithub />
        </Link>
        <Link target="_blank" href="https://dev.to/sbeaury" rel="noreferrer">
          <FaDev />
        </Link>
        <Link target="_blank" href="mailto:sbeaury@gmail.com">
          <MdEmail />
        </Link>
      </LinkContainer>
    </Section.Container>
  )
}

export default Intro
